<template>
  <div class="item">
    <div class="image"><img :src="data.image"></div>
    <div class="name">
      <div class="cn-name">{{data.cnName}}</div>
      <div class="en-name">{{data.enName}}</div>
    </div>
  </div>
</template>

<script>
  export default{
    components: {},
    props: {
      data: {
        type: Object,
        required: true
      }
    }
  }

</script>

<style scoped lang="stylus">
  @import '../assets/stylus/variables.styl'
  .item {
    padding 0.1rem
    background-color: #fff;
    width: 100%;
    .image {
      font-size 0
      width 1.50rem
      height 0.50rem
      img {
        height 100%
        width 100%
        display block
      }
      margin-bottom 0.17rem
    }
    .name {
      border-top 1px solid #eee
      padding-top 0.05rem
      font-size 0.12rem
      color $baseBlueColor
      .cn-name {
        margin-bottom 0.04rem
      }
      .en-name {
      }
    }
  }

</style>



