<template>
  <div class="item">
    <div class="item-container">
      <div class="collage">
        <div class="logo"><img :src="data.college.image"></div>
        <div class="name">
          <div class="cn-name">{{data.college.cnName}}</div>
          <div class="en-name">{{data.college.enName}}</div>
        </div>
      </div>
    </div>
    <div class="grade">
      <table>
        <thead>
        <tr>
          <td style="font-size:0.14rem">专业</td>
          <td>雅思<br/>{{data.college.demand.IELTS}}</td>
          <td>托福<br>{{data.college.demand.TOEFL}}</td>
          <td>听<br>{{data.college.demand.listening}}</td>
          <td>说<br>{{data.college.demand.speaking}}</td>
          <td>读<br>{{data.college.demand.reading}}</td>
          <td>写<br>{{data.college.demand.writing}}</td>
          <td>专业成绩<br>{{data.college.demand.majorGrade}}</td>
          <td>专业工作<br>{{data.college.demand.majorWork}}</td>
          <td>　</td>
        </tr>
        </thead>
        <tbody v-if="data.selectedMajors">
        <tr v-for="major in data.selectedMajors">
          <td>{{major.name}}</td>
          <td :style="{color:data.college.demand.IELTS>=major.IELTS?'#6c6c6c':'#fbaa40'}">{{major.IELTS}}</td>
          <td :style="{color:data.college.demand.TOEFL>=major.TOEFL?'#6c6c6c':'#fbaa40'}">{{major.TOEFL}}</td>
          <td :style="{color:data.college.demand.listening>=major.listening?'#6c6c6c':'#fbaa40'}">{{major.listening}}
          </td>
          <td :style="{color:data.college.demand.speaking>=major.speaking?'#6c6c6c':'#fbaa40'}">{{major.speaking}}</td>
          <td :style="{color:data.college.demand.reading>=major.reading?'#6c6c6c':'#fbaa40'}">{{major.reading}}</td>
          <td :style="{color:data.college.demand.writing>=major.writing?'#6c6c6c':'#fbaa40'}">{{major.writing}}</td>
          <td :style="{color:data.college.demand.majorGrade>=major.majorGrade?'#6c6c6c':'#fbaa40'}">
            {{major.majorGrade}}
          </td>
          <td :style="{color:data.college.demand.majorWork>=major.majorWork?'#6c6c6c':'#fbaa40'}">{{major.majorWork}}
          </td>
          <td @click="majorsDel($index)">X</td>
        </tr>
        </tbody>
      </table>

    </div>
  </div>
</template>

<script>
  export default{
    components: {},
    methods: {
      majorsDel(idx){
        console.log('del ' + idx);
        console.log(this.data);
        this.data.selectedMajors.splice(idx, 1);

      }
    },
    props: {
      data: {
        type: Object,
        required: true
      }
    }
  }
</script>

<style scoped lang="stylus">
  .item {
    background-color #fff
    margin-top 0.1rem

    .collage {
      width 100%;
      height 0.8rem
      display flex
      justify-content space-between
      align-items center
      .logo {
        font-size 0;
        width 1.54rem;
        height 0.5rem;
        img {
          display block
          height 100%;
          width 100%;
        }
      }
      .name {
        display flex
        flex-direction column
        align-items flex-end
        justify-content space-around
        height 0.5rem
        .cn-name {
          font-size .16rem;
          color #0e2d60
        }
        .en-name {
          font-size .14rem;
          color #0e2d60
        }
      }
    }

    .grade {
      font-size 0.12rem;
      border-top 1px solid #eee
      color #6c6c6c
      table {
        thead {
          line-height 0.2rem
          vertical-align top
        }
        margin-top 0.05rem
        width 100%
        text-align center
        tbody {
          color #1e1e1e
          line-height 0.3rem
          tr {
            &:nth-child(2n-1) {
              background-color #f3f3f3
            }
          }
        }
      }
    }
  }

  .item-container {
    margin 0 auto;
    width 3.50rem

  }

</style>



