<template>
    <div class="cs-main">
        <div class="head-top">
            <compass-header title="院校筛选"></compass-header>
        </div>
        <div class="select-list">
            <ul>
                <a href="#"><li>地点<span class="fright">{{ query.country }} · 新南威尔士洲 · 全部&nbsp  ></span></li></a>
                <a href="#"><li>选择专业<span class="fright">{{ query.majoy }}&nbsp  ></span></li></a>
                <a href="#"><li>学校类型<span class="fright">{{ query.clgType }}&nbsp  ></span></li></a>
                <a href="#"><li>国内排名<span class="fright">{{ query.array }}&nbsp  ></span></li></a>
            </ul>
        </div>
    </div>
</template>
<script>
import CompassHeader from '../components/CompassHeader'
export default {
    data() {
            return {
                query: {
                    country: '澳大利亚',
                    province: '新男威尔士洲',
                    city: '全部',
                    majoy: '艺术与设计',
                    clgType: '不限',
                    array: '9-20名',
                }
            }
        },

        components: {
            CompassHeader
        }
}
</script>
<style>
.cs-main {
    height: 3rem;
    background-color: #fff;
}

.head-top {
    height: 1rem;
    border-bottom: 1px solid #efefef;
}

.select-list ul li {
    height: .5rem;
    line-height: .5rem;
    border-bottom: 1px solid #efefef;
    padding: 0 .08rem;
    font-size: .14rem;
    /* float: left; */
}

</style>



