<template>
  <div class="list">
    <div class="item-wrapper" v-for="item in items">
      <person-collection-item :data="item"></person-collection-item>
    </div>
  </div>
</template>

<script>
  import PersonCollectionItem from './PersonCollectionItem'
  export default{
    components: {PersonCollectionItem},
    props: {
      items: {
        type: Array,
        default(){
          return [];
        }
      }
    }

  }

</script>

<style scoped lang="stylus">
  .list {
    overflow hidden
    .item-wrapper {
      float left
      margin 0.1rem 0 0 0.11rem;
      width: 1.72rem;
      height: 1.24rem;
    }
  }

</style>



