<template>
    <header class="header">
        <compass-header></compass-header>
        <!-- <div class="serch-box"> -->
        <serch-box class="serch-box"></serch-box>
        <!-- </div> -->
    </header>
    <a href="#">
        <div class="select-collage">
            <div class="content">
                <span class="c1-ly">· {{ country }}</span>
                <span class="c-ly">· {{ province }}</span>
                <span class="c-ly">· {{ city }}</span>
                <span class="c-ly">· {{ majoy }}</span>
            </div>
            <span class="choice">
          >
        </span>
        </div>
    </a>
    <div class="sort">
        <div class="content">
            为您找到相关的<span style=" color: #42D9CB; font-size: .18rem ">
        {{ num }} </span>所学校
        </div>
        <div class="sort-ico">
            排序 <i><img src="../assets/images/arrow-down.png" 
                                  height="10" width="10" alt=""></i>
        </div>
    </div>
    <div style="clear: both;"></div>
    <!-- 卡片盒子 -->
    <div class="card-box">
        <div class="clg-card" id="clg-card" v-for=" result in results  ">
            <a href="#">
                <!-- logo -->
                <div class="clg-logo">
                    <img src="../assets/images/ap1.png" style="width: 102%; margin-bottom: .4rem;" alt="">
                </div>
                <div class="cname">
                    <p>{{result.cname}}</p>
                </div>
                <div class="ename">
                    <p>{{result.ename}}</p>
                </div>
            </a>
        </div>
        <div class="clg-card" id="clg-card">
        </div>
        <div class="clg-card" id="clg-card">
        </div>
        <div class="clg-card" id="clg-card">
        </div>
        <div class="clg-card" id="clg-card">
        </div>
        <div class="clg-card" id="clg-card">
        </div>
    </div> <!-- end card box -->
</template>

<script>
import CompassHeader from '../components/CompassHeader'
import SerchBox from '../components/SerchBox'

export default {
    data() {
            return {
                IMGROOT: '/src/assets/images',
                country: '澳大利亚',
                province: '新男威尔士洲',
                city: '墨尔本',
                majoy: '设计与艺术系统',
                num: 8,
                results: [{
                        logoURL: ' assets/images/ap1.png',
                        cname: '悉尼大学',
                        ename: 'The Universitu Sydney'
                    }, {
                        logoURL: ' assets/images/ap2.png',
                        cname: '耶鲁大学',
                        ename: 'The Universitu Sydney'
                    }, {
                        logoURL: ' assets/images/ap3.png',
                        cname: '悉尼大学',
                        ename: 'The Universitu Sydney'
                    },

                ]
            }
        },
        components: {
            CompassHeader,
            SerchBox
        }
}
</script>
<style>
.header {
    background-color: #ffffff;
    width: 100%;
}

.serch-box {
    margin: -.13rem auto;
    width: 95%;
}

.select-collage {
    margin-top: .14rem;
    height: .5rem;
    font-size: .14rem;
    line-height: .5rem;
    background-color: #ffffff;
}

.select-collage .content {
    overflow: hidden;
    float: left;
    /* 文本不换行 */
    white-space: nowrap;
    /* 超出部分显示... */
    text-overflow: ellipsis;
    width: 95%;
}

.c1-ly {
    margin-left: .15rem;
}

.c-ly {
    margin-left: .05rem;
}

.choice {
    float: left;
}

.sort {
    margin-top: .06rem;
    height: .5rem;
    font-size: .14rem;
    color: #7A8F8B;
    line-height: .5rem;
    background-color: #ffffff;
}

.sort .content {
    margin-left: 5%;
    float: left;
}

.sort .sort-ico {
    float: right;
    margin-right: 2%;
}


/* 卡片盒子 */

.card-box {
    /* 有些bug */
    /*  display: flex;
    margin-top: .05rem;
    flex-wrap: wrap;
    justify-content: center; */
    /* float: left; */
    overflow: hidden;
    margin: .04rem auto;
}

.clg-card {
    float: left;
    position: relative;
    left: 2%;
    height: 1.25rem;
    width: 46%;
    margin: .04rem .04rem;
    padding: .08rem .08rem;
    background-color: #fff;
}

.clg-card .clg-logo {
    height: .70rem;
    border-bottom: 1px solid #efefef;
    /* padding-bottom: .2rem; */
}

.clg-card .cname {
    padding-top: .05rem;
    font-size: .13rem;
}

.clg-card .ename {
    padding-top: .05rem;
    font-size: .13rem;
}
</style>



