<template>
  <div class="list">
    <div class="item-wrapper" v-for="item in items">
      <person-message-item :data="item"></person-message-item>
    </div>
  </div>
</template>

<script>
  import PersonMessageItem from './PersonMessageItem'

  export default{
    components: {PersonMessageItem},
    props: {
      items: {
        type: Array,
        default(){
          return [];
        }
      }
    }
  }
</script>

<style scoped lang="stylus">

  .list {
    margin-top 0.1rem
    .item-wrapper {
      border-bottom 1px solid #eee;
    }
  }
</style>



