<template>
    <div class="main">
        <div class="head-top">
            <compass-header title="学校类型"></compass-header>
        </div>
        <div class="m-container">
            <ul>
                <li id="li-0" @click=" changeClass(0) ">不限</li>
                <li v-for=" type in typeList " id="li-{{ $index+1 }}" :class=" { selected : isSelected } " @click=" changeClass($index+1) ">{{ type.name }}</li>
            </ul>
        </div>
    </div>
    <!-- 页面底部 -->
    <div class="bottom">
        <div class="b-left">
            <input type="text">
            <span style=" margin: 0 .1rem;"> - </span>
            <input type="text">
            <span>  &nbsp	名</span>
            <button class="btn-right">
        确定
        </button>
        </div>
        
    </div>
</template>
<script>
import CompassHeader from '../components/CompassHeader'
import LoginForm from '../components/LoginForm'

export default {
    data() {
            return {
                typeList: [{
                    name: '1-8',
                    isSelected: false,
                }, {
                    name: '9-20',
                    isSelected: false,
                }, {
                    name: '21-50',
                    isSelected: false,
                }, ]
            }
        },
        methods: {
            changeClass(index) {
                let node = document.getElementById('li-' + index);
                // console.log(node)
                node.style.backgroundColor = '#0D2F60';

                // console.log(type.isSelected);
                // type.isSelected = !type.isSelected;
            },
        },
        components: {
            CompassHeader,
            LoginForm,
        },

}
</script>
<style scoped>
.m-container {
    width: 100%;
}

.m-container ul li {
    height: .51rem;
    width: 100%;
    padding-left: 2%;
    line-height: .51rem;
    border-bottom: 1px solid #ebebeb;
}

.selected {
    background-color: #0D2F60;
}

.bottom {
    margin-top: .42rem;
    padding: 0 .08rem;
    height: .48rem;
    line-height: .48rem;
    background-color: #fff;
    font-size: .14rem;
}

.b-left {
    /* float: left; */
}

.b-left input {
    height: .36rem;
    width: .57rem;
    border: none;
    background-color: #ebebeb;
}

.btn-right{
	margin-top: .06rem;
	float: right;
	height: .36rem;
	 width: .70rem;
background-color: #FF8450;
border: none;
color: #fff;
	
}
</style>



