import Vue from 'vue'
import App from './App'

Vue.filter('date', function (value, format) {
  var date = new Date(value);
  var m = (date.getMonth() + 1) + '';
  m = m.length <= 1 ? 0 + m : m;
  var d = date.getDate();
  d = d.length <= 1 ? 0 + d : d;
  return date.getFullYear() + "-" + m + "-" + d;
});



/* eslint-disable no-new */
new Vue({
  el: 'body',
  components: {App}
})



