<template>
  <compass-header :need-fix-height="false"></compass-header>

  <div class="header">
    <div class="avatar-wrapper">
      <img :src="data.userAvatar">
    </div>
    <div class="username">{{data.username}}</div>
  </div>
  <person-nav @nav-to="navTo"></person-nav>


  <person-message-list v-if="activeTabIdx==0" :items="data.messages"></person-message-list>
  <person-collection-list v-if="activeTabIdx==1" :items="data.collections"></person-collection-list>
  <person-willing-list v-if="activeTabIdx==2" :items="data.willings"></person-willing-list>
</template>

<script>
  import CompassHeader from '../components/CompassHeader'
  import PersonNav from '../components/PersonNav'
  import PersonMessageList from '../components/PersonMessageList'
  import PersonCollectionList from '../components/PersonCollectionList'
  import PersonWillingList from '../components/PersonWillingList'


  export default{
    components: {CompassHeader, PersonNav, PersonMessageList, PersonCollectionList, PersonWillingList},
    props: {
      activeTabIdx: {
        type: Number,
        default: 0
      },
      data: {
        type: Object,
        default(){
          return {
            messages: [],
            collections: [],
            willings: []
          };
        }
      }
    },

    methods: {
      navTo(idx){
        this.activeTabIdx = idx;
      }
    }
  }
</script>

<style scoped lang="stylus">
  .header {
    padding-top 0.44rem
    background-color: #fff;
    height: 2.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction column

    .avatar-wrapper {
      font-size 0;
      height: 0.6rem;
      width: 0.6rem
      border-radius 100rem;

      img {
        display block
        width 100%
        height 100%
      }
    }
    .username {
      margin-top .25rem
      font-size 0.16rem
      font-weight 700
    }
  }


</style>



