<template>
  <div class="modal-background" v-if="show">
    <div class="modal-wrapper">
      <div class="close-btn"></div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default{
    components: {},
    props: {
      needClose: {
        type: Boolean,
        default: true
      },
      show: {
        type: Boolean,
        default: true
      }
    }

  }
</script>

<style scoped lang="stylus">
  .modal-background {
    top 0;
    left 0;
    right 0;
    bottom 0;
    position fixed
    display flex
    align-items center
    justify-content center

    background-color rgba(48, 48, 48, 0.75);
    .modal-wrapper {
      width 3.55rem;
      background #fff;
      position relative
      .close-btn:before {
        color #777
        font-size .16rem
        content 'x'
        display block
        position: absolute
        right .1rem
        top .1rem
      }
    }
  }

</style>



