<template>
    <div class="sa-main">
        <div class="head-top">
            <compass-header title="选择专业"></compass-header>
        </div>
        <div class="m-container">
            <ul>
                <li id="li-0" @click=" changeClass(0) ">不限</li>
                <li v-for=" major in majorList " id="li-{{ $index+1 }}" :class=" { selected : isSelected } " @click=" changeClass($index+1) ">{{ major.name }}</li>
            </ul>
        </div>
    </div>
</template>
<script>
import CompassHeader from '../components/CompassHeader'
import LoginForm from '../components/LoginForm'

export default {
    data() {
            return {
                majorList: [{
                    name: '艺术与设计',
                    isSelected: false,
                }, {
                    name: '经济与金属',
                    isSelected: false,
                }, {
                    name: '社会科学',
                    isSelected: false,
                }, {
                    name: '人文科学',
                    isSelected: false,
                }, {
                    name: '工程学',
                    isSelected: false,
                }, {
                    name: '医学',
                    isSelected: false,
                }, ],
                isSelected: false
            }
        },

        methods: {
            changeClass(index) {
                let node = document.getElementById('li-' + index);
                // console.log(node)
                node.style.backgroundColor = '#0D2F60';

                // console.log(major.isSelected);
                // major.isSelected = !major.isSelected;
            },
        },
        components: {
            CompassHeader,
            LoginForm,
        },

}
</script>
<style scoped>
.m-container {
    width: 100%;
}

.m-container ul li {
    height: .51rem;
    width: 100%;
    padding-left: 2%;
    line-height: .51rem;
    border-bottom: 1px solid #ebebeb;
}

.selected {
    background-color: #0D2F60;
}
</style>



