	module.exports = " <div class=main _v-7c84c9c4=\"\"> <compass-header _v-7c84c9c4=\"\"></compass-header> <school-top _v-7c84c9c4=\"\"></school-top> <div style=padding-bottom:10px _v-7c84c9c4=\"\"> <div style=margin-top:30px class=item-body _v-7c84c9c4=\"\"> <div class=s-left _v-7c84c9c4=\"\"> <img class=item-img src=" + __webpack_require__(106) + " _v-7c84c9c4=\"\"> </div> <div class=s-right _v-7c84c9c4=\"\"> 悉尼 • since 1850 </div> </div> <div style=margin-top:10px class=item-body _v-7c84c9c4=\"\"> <div class=s-left _v-7c84c9c4=\"\"> <img class=item-img src=" + __webpack_require__(107) + " _v-7c84c9c4=\"\"> </div> <div class=s-right _v-7c84c9c4=\"\"> 0923-234935621 </div> </div> <div style=margin-top:10px class=item-body _v-7c84c9c4=\"\"> <div class=s-left _v-7c84c9c4=\"\"> <img style=height:12px class=item-img src=" + __webpack_require__(108) + " _v-7c84c9c4=\"\"> </div> <div class=s-right _v-7c84c9c4=\"\"> www.sydney.edu.au </div> </div> </div> <div class=ranking _v-7c84c9c4=\"\"> <div class=rank-name _v-7c84c9c4=\"\">院校排名</div> <div class=rank-img2 _v-7c84c9c4=\"\"><img style=height:12px src=" + __webpack_require__(110) + " _v-7c84c9c4=\"\"></div> <div class=rank-img1 _v-7c84c9c4=\"\"><img style=height:7px src=" + __webpack_require__(109) + " _v-7c84c9c4=\"\"></div> </div> <div class=profile _v-7c84c9c4=\"\"> <span class=profile-title _v-7c84c9c4=\"\">•简介</span> <div class=profile-text _v-7c84c9c4=\"\"> <br _v-7c84c9c4=\"\"> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;成立于1850年悉尼大学从一开始就革新改变了联合王国的古典大学以血统而不以成绩收生的传统。学者和政客的激烈争论促使英国和北美的大学随之改变以应社会发展，悉尼大学亦成为了其他殖民地新兴大学的榜样。<br _v-7c84c9c4=\"\">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 1858年2月27日，维多利亚女王授予悉尼大学皇家宪章，使其与英国的大学拥有相同的地位。<br _v-7c84c9c4=\"\">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 在19世纪下半期，悉尼大学形成了助学和捐赠的文化潮流，Charles Badham教授就是其中的先驱者，他认为所有有能力的学生都应当享有大学教育并争取设立奖学金以应学生在攻读过程中的不时之需。<br _v-7c84c9c4=\"\">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 时至今日，悉尼大学每年都会收到澳洲大学最高的捐赠，而2012年每八位悉尼大学的在读学生，就有一位享有大学奖学金的资助。 <br _v-7c84c9c4=\"\">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 从1881年起，悉尼大学开始招收女生，成为世界上最早允许女生授学的学府之一，比起牛津大学或剑桥大学早了至少数十年。<br _v-7c84c9c4=\"\">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 1901年，悉尼大学的毕业生，埃德蒙·巴顿建立了澳大利亚联邦并成为了澳大利亚第一任总理。<br _v-7c84c9c4=\"\">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 悉尼大学的1800名师生参加了第一次世界大战，197人牺牲，4000名师生参加了第二次世界大战，250人牺牲，今天在悉尼大学钟楼下面，仍能找到纪念他们的饰板。 </div> </div> <div class=profile _v-7c84c9c4=\"\"> <span class=profile-title _v-7c84c9c4=\"\">•校园生活</span> <div class=profile-text _v-7c84c9c4=\"\"> <br _v-7c84c9c4=\"\"> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;澳洲国立大学的主校区位在澳洲首都堪培拉的阿克顿（Acton）社区，校园总面积约150公顷，超过200栋建筑与10,000棵树木，邻近黑山（Black Mountain）、伯利·格里芬湖（Lake Burley Griffin）与堪培拉市中心。<br _v-7c84c9c4=\"\">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; ANU校园内设有银行、邮局、书店、药妆店、贩卖部、咖啡屋、小酒吧、餐厅、旅行社，坐拥南半球规模最大的图书馆；另备有完善的体育设施、学生医疗中心、画廊、博物馆、音乐厅等。<br _v-7c84c9c4=\"\">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 此外，堪培拉座落众多的国家级文化和学术机构，如澳洲联邦科学与工业研究组织（CSIRO）、澳洲科学院（AAS）、澳洲人文科学院、澳洲社会科学院（ASSA）、澳洲国家植物园、澳洲国家图书馆、澳洲国立美术馆、澳洲国立博物馆、澳洲国家档案馆、澳洲国立影片与声音档案馆、澳洲国家生物标准实验室、澳洲地质调查组织（AGSO）、澳洲犯罪学研究所（AIC）、澳洲战争纪念馆、国家肖像美术馆、堪培拉博物馆暨美术馆和国家首都展览馆等等。ANU与这些机构有着紧密的联系和合作。 <img style=width:100%;margin-top:10px src=" + __webpack_require__(113) + " _v-7c84c9c4=\"\"> </div> </div> <div class=profile _v-7c84c9c4=\"\"> <span class=profile-title _v-7c84c9c4=\"\">•地理位置</span> <div class=profile-text _v-7c84c9c4=\"\"> <br _v-7c84c9c4=\"\"> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;悉尼大学主要校区紧靠新南威尔士的首府悉尼市中心西南，占地72公顷靠近中央车站及唐人街，离中心商业区仅5分钟的路程。其它校园分布在城市中心及其周围。大学还有六间分校，它们分别是：位于悉尼市郊的Mallett Street校区，有商科和护理等主要学院；位于悉尼市港口区的法学院，主设法律专业；位于悉尼市港口区的音乐学院；位于正校以西16公里的Cumbeland校区，有健康科学和护理专业；位于正校以西65公里的Camden校区，以农科、兽医见长；位于新南威尔斯省中部小镇的Orange农业学院，主设农业管理专业。 <img style=width:100%;margin-top:10px src=" + __webpack_require__(103) + " _v-7c84c9c4=\"\"> </div> </div> </div> ";

