<template>
  <div class="nav">
    <div class="item-wrapper" v-for="item in items" :class="{active:idx==$index}">
      <a class="item" @click="onClick($index)">{{item}}</a>
    </div>
  </div>
</template>

<script>
  export default{
    components: {},
    props: {
      items: {
        type: Array,
        default: function () {
          return ['消息', '收藏', '意向单'];
        }
      }

    },
    data(){
      return {idx:1};
    },
    ready(){
      this.$emit('nav-to', this.idx);
    },
    methods: {
      onClick($index){
        this.idx = $index;
        this.$emit('nav-to', $index);

        return false;
      }
    }
  }
</script>

<style scoped lang="stylus">

  @import '../assets/stylus/variables.styl'
  .nav {
    border-top 1px solid #ebebeb
    height 0.45rem
    background-color: #fff;
    font-size 0.14rem
    display flex
    align-items stretch
    justify-content center
    .item-wrapper {
      margin 0 .2rem
      &.active {
        border-bottom 2px solid $baseBlueColor;
        color $baseBlueColor
      }
      .item {
        color inherit
        display block
        line-height 0.45rem
      }

    }
  }

</style>



