<template>
  <form>
    <div class="form-control">
      <text-input placeholder="姓名"></text-input>
    </div>
    <div class="form-control">
      <text-input placeholder="最近就读院校"></text-input>
    </div>
    <div class="form-control">
      <compass-select name="test" placeholder="请选择专业"
                      :options="[{text:'test',value:'test'},{text:'test2',value:'test2'}]"></compass-select>
    </div>
    <div class="form-control">
      <div class="control-group">
        <compass-select name="test" placeholder="选择省份"
                        :options="[{text:'test',value:'test'},{text:'test2',value:'test2'}]"></compass-select>
        <text-input placeholder="高考成绩"></text-input>
      </div>
    </div>
    <div class="form-control">
      <div class="control-group">
        <compass-select name="test" placeholder="雅思"
                        :options="[{text:'test',value:'test'},{text:'test2',value:'test2'}]"></compass-select>
        <text-input placeholder="0-9分"></text-input>
      </div>
    </div>
    <div class="submit">
      <submit-button background-color="#1ddab0" text="生成选校方案" font-size="0.16rem"></submit-button>
      <submit-button background-color="#a0a0a0" text="返回" font-size="0.16rem"></submit-button>
    </div>

  </form>
</template>

<script>
  import CompassSelect from './CompassSelect'
  import SubmitButton from './SubmitButton'
  import TextInput from './TextInput'
  export default{
    components: {CompassSelect, SubmitButton, TextInput}
  }
</script>

<style lang="stylus" scoped>
  form {
    padding-top: 0.4rem;
  }

  .form-control {
    margin-bottom: .1rem;
  }

  .submit {
    margin-top: 0.5rem;
    div:nth-child(1){
      margin-bottom 0.1rem
    }
  }

  .control-group {
    display: flex;
    & > select:first-child {
      width 1.05rem
    }
  }
</style>



