<template>
  <select>
    <option value="" selected v-if='placeholder'>{{placeholder}}</option>
    <option :value="option.value" v-for="option in options">{{option.text}}</option>
  </select>
</template>

<script>
  export default{
    props: {
      placeholder: {
        type: String,
        default: ''
      },
      options: {
        type: Array,
        default: []
      },
      name: {
        type: String,
        required: true
      }
    }
  }
</script>

<style lang="stylus" scoped>

  select {
    width 100%
    border none
    height 0.4rem;
    padding 0 1em;
    -webkit-appearance: none;
  }
</style>



