<template>
  <div class="item">
    <div class="image"><img :src="data.image"></div>
    <div class="message">
      <div class="title">{{data.title}}</div>
      <div class="content">{{data.content}}</div>
    </div>
    <div class="time">{{data.time | date}}</div>
  </div>
</template>

<script>
  export default{
    components: {},
    props: {
      data: {
        type: Object,
        required: true
      }
    }
  }
</script>

<style scoped lang="stylus">
  $topMargin = 0.08rem
  .item {
    font-size: 0.14rem;
    background-color: #fff;
    display: flex;
    align-items: flex-start;
    justify-content space-around
    .image {
      font-size: 0;
      height 0.45rem
      width 0.45rem
      border-radius 0.05rem
      margin $topMargin
      img {
        height 100%
        width 100%
      }
    }
    .message {
      width 2.05rem;
      padding-left 0.14rem

      margin-top $topMargin
      .title {
        font-size 0.18rem;
        margin-bottom 0.05rem
      }
      .content {
        font-size: 0.16rem;
        color #aaa
      }
    }
    .time {
      margin-top $topMargin
      color #aaa
    }
  }


</style>



