<template>
    <div class="sa-main">
        <compass-header title="选择地点"></compass-header>
        <div class="left">
            <ul class="content-item-l" >
            <li>不限</li>
                <li v-for="country in countryList">{{country.name}}</li>
            </ul>
        </div>
        <div class="right">
            <div class="content-item-r">
              <div class="cir-top">
                全部
              </div>
              <ul class="cir-list">
                <li>全部</li>
              </ul>
            </div> <!-- content item -->
            <div class="content-item-r">
              <div class="cir-top">
                新威尔士洲
              </div>
              <ul class="cir-list">
                <li>全部</li>
                <li>墨尔本</li>
                <li>巴拉瑞特</li>
              </ul>
            </div>
            <div class="content-item-r">
              <div class="cir-top">
                全部
              </div>
              <ul class="cir-list">
                <li>全部</li>
                <li>墨尔本</li>
                <li>巴拉瑞特</li>
              </ul>
            </div>
            
        </div> <!-- end right -->
    </div>
</template>
<script>
import CompassHeader from '../components/CompassHeader'
import LoginForm from '../components/LoginForm'



export default {
    data() {
      return{
      countryList: [
      { name: '澳大利亚' },
      { name: '新西兰'},
      { name: '美国'},
      ]
    }
    },
    components: {
        CompassHeader,
        LoginForm
    }
}
</script>
<style>
.sa-main {
    overflow: hidden;
    font-size: .14rem;
    background-color: #fff;
}

.left {
    float: left;
    width: 30%;
    margin: 1.3rem .15rem;
}

.content-item-l li{
  height: .60rem;
}

.right{
  float: right;
  width: 62%;
  margin-top: .83rem;
}

.content-item-r{
  /* margin-bottom: .1rem; */
  overflow: hidden;
}

.cir-top{
  border-bottom: 1px solid #2f2f2f;
  padding: 0 0 .02rem 0;
}

.cir-list{
  float: right;
  padding: .18rem .15rem
}

.cir-list li{
  height: .3rem;
  text-align: right;
}

</style>



