<template>
  <div class="list">
    <div class="item-wrapper" v-for="item in items">
      <person-willing-item :data="item"></person-willing-item>
    </div>
  </div>
</template>

<script>
  import PersonWillingItem from './PersonWillingItem'
  export default{
    components: {PersonWillingItem},
    props: {
      items: {
        type: Array,
        default(){
          return [];
        }
      }
    }
  }
</script>

